.navigation-menu{
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navigation-menu li{
    margin: 0px 30px;
}

.navigation-menu li a{
    text-decoration: none;
    color: white;
}

.active{
    color: goldenrod !important;
    font-weight: bold !important;
    
}
/* mobile view */

.mobile-navigation{
    list-style: none;

}

.mobile-navigation li{
    margin-top: 20px;
}

.mobile-navigation a{
    text-decoration: none;
    font-size: 1.4rem;
    color: black;
}



